export class HR implements ApiUser {

  //implement ApiUser
  readonly id: string;
  firebaseToken: string;

  email: string;

  avatar_url: string;

  first_name: string;

  description: string;

  last_name: string;

  start_date: string;

  company_name: string;

  constructor(id: string) {
    this.id = id;
  }

}



